<template>
    <div class="row">
      <div class="col-3 col-md-3"
       v-for="sensor in sensorlist" :key="sensor.id">    
            <StatisticCardHorizontal
            :icon="sensor.icon" :statistic="sensor.sensor_value + ' ' + sensor.unit" :statistic-title="sensor.sensor_name" style="max-width:500px"
        />
      </div>
    </div>
</template>

<script>
import axios from 'axios'
import StatisticCardHorizontal from '@core/components/statistics-cards/StatisticCardHorizontal.vue'
import { BCard, BCardText, BLink } from 'bootstrap-vue'
export default {
    components: {
    BCard,
    BCardText,
    BLink,
    StatisticCardHorizontal,
  },
    data() {
        return{
            sensorlist: []
        }
    },
    methods: {
        event(){
        axios.get('http://127.0.0.1:8000/api/sensor')
        .then(response => (this.sensorlist = response.data))
        }
    },
    mounted () {
    this.event()
    this.timer = setInterval(this.event, 10000)
  }
}
</script>